@import url(https://fonts.googleapis.com/css2?family=MonteCarlo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  max-height: 100%;
  max-width: 100%;
  text-align: center;
}

.title {
  color: aliceblue;
  font-family: MonteCarlo , cursive;
  font-weight: 700;
  margin-top: 4vh;
  text-align: center;
  text-shadow: 2px 2px black;
}


.loginBox {
  border: 2px grey;
  display: flex;
  flex-direction: column;
  height: 360px;
  margin-top: 30vh;
  text-align: center;
  width: 640px;
}

video {
  background-size: cover;
  bottom: 0;
  height: auto;
  min-height: 100%;
  min-width: 100%;
  position: fixed;
  right: 0;
  width: auto;
  z-index: -1000;
}

.header {
  align-items: center;
  background-color: salmon;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  position: fixed;
  width: 100%;
}

.headerbox {
  align-items: center;
  display: flex-box;
  height: 60px;
  justify-content: space-between;
  margin: 0 auto;
  width: 100%;
}

.intheader {
  align-items: center;
  display: flex;
}

.inputsearch {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 60px;
  z-index: 1;
}

footer {
  align-items: center;
  background-color: salmon;
  bottom: 0;
  display: flex;
  height: 60px;
  justify-content: space-evenly;
  margin: 0;
  position: fixed;
  width: 100%;
}

.searchbar {
  background-color: azure;
  box-sizing: content-box;
  display: flexbox;
  height: 60px;
  position: absolute;
  top: 60px;
  width: 100%;
  z-index: 1;
}

.link {
  box-shadow: 1px , 1px;
  color: black;
  text-decoration: none;
}

input {
  margin-left: 5px;
  margin-right: 5px;
}

.cardlist {
  background-image: linear-gradient(to bottom, salmon, white, salmon);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 0 auto;
  padding-bottom: 45px;
  top: 0;
}

.foodimg {
  height: 150px;
  width: auto;
}

section {
  margin: 5px;
}

* {
  text-align: center;
}

.perfilpage {
  align-content: center;
  align-items: center;
  align-self: center;
  display: flex;
  flex-direction: column;
  float: inline-start;
  height: 360px;
  justify-content: center;
  margin-top: 20%;
  vertical-align: center;
}

.button-one {
  align-items: center;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  margin: 20px;
  position: relative;
  text-align: center;
  width: 200px;
}

.button-two {
  align-items: center;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  margin: 20px;
  position: relative;
  text-align: center;
  width: 200px;
}

.button-three {
  align-items: center;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  margin: 20px;
  position: relative;
  text-align: center;
  width: 200px;
}

.cardlist {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 0 auto;
  top: 0;
}


.foodimg {
  height: 150px;
  width: auto;
}

section {
  margin: 5px;
}

.link {
  box-shadow: 1px , 1px;
  color: black;
  text-decoration: none;
}


.recomendation-card {
  align-items: center;
  border: 1px solid black;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  margin: 3% 3% 8%;
}

.recomendation-card img {
  border-radius: 20px 20px 0 0;
  max-height: auto;
  max-width: 170px;
  scroll-snap-align: start;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.recomendation-card p {
  font-size: 15pt;
  margin-top: 1%;
}

* {
  margin: 0;
}

.img-recipe {
  border: 2px solid black;
  width: 100%;
}

.recommended {
  border: 2px solid black;
  width: 100%;
}

.start-recipe-btn {
  bottom: 0;
  position: fixed;
}

body {
  background-color: white;
}

.start-recipe-btn:disabled {
  font-size: 18px;
  padding: 10px 20px;
}

.start-recipe-btn:enabled {
  background-color: salmon;
  border-radius: 10px;
  font-size: 18px;
  padding: 10px 85px;
}

